import React from "react";
import { useState, useEffect } from "react";
export default function OrderDetail() {
  let [data, setData] = useState([]);

  useEffect(() => {
    let data = JSON.parse(localStorage.getItem("cart") || "[]");
    setData(data);
    setTimeout(() => {
      console.log("b", data);
    }, 0);
  }, []);

  const handleDelete = (bdata: any) => {
    console.log("bookdata.onDelete: ", bdata);
    console.log("all data", data);

    data = data.filter((d: any) => d.id !== bdata.id);
    setData(data);

    localStorage.setItem("cart", JSON.stringify(data) || "[]");
    console.log(data, "datass");
  };
  const handleSubmit = () => {
    alert("Your Order Placed Successfully");
  };
  if (data.length === 0) {
    return (
      <div className="d-flex justify-content-centerd-flex justify-content-center mt-5 pt-5">
        <h1 className="mt-5 ml-5">Cart is Empty! Please add something </h1>{" "}
      </div>
    );
  }

  return (
    <div className="ml-3 mr-3">
      <p className="ml-3 mt-5 pt-3">There are "{data.length}" products in cart</p>
      <table className="table table-hover">
        <thead>
          <tr>
            <th className="text-center h-25 w-25" scope="col">
              Image
            </th>
            <th className="text-center" scope="col">
              Title
            </th>
            <th className="text-center" scope="col">
              Author
            </th>
            <th className="text-center" scope="col">
              Quantity
            </th>
            <th className="text-center" scope="col">
              Price
            </th>
            <th className="text-center" scope="col">
              Total
            </th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {data.map((d: any) => (
            <tr key={d.id}>
              <td className="text-center">
                <img className="w-50 h-50" src={d.book_img}></img>
              </td>
              <td className="text-center">{d.title}</td>
              <td className="text-center">{d.author_name}</td>
              <td className="text-center">{d.qty}</td>
              <td className="text-center">{d.price}</td>
              <td className="text-center">{d.price * d.qty}</td>
              <td>
                <button
                  type="button"
                  className="btn btn-danger"
                  onClick={() => handleDelete(d)}
                >
                  DELETE
                </button>
              </td>
            </tr>
          ))}
          <td></td>
          <td></td>
          <td className="d-flex justify-content-around  text-center">Total:</td>
          <td colSpan={1}>
            <p className="d-flex justify-content-around mr-1 text-center">
              {data.reduce((total, item: any) => total + item.qty, 0)}
            </p>
          </td>
          <td colSpan={2}>
            <p className="d-flex justify-content-end mr-4 text-center">
              {data.reduce(
                (total, item: any) => total + item.price * item.qty,
                0
              )}
            </p>
          </td>
          <td></td>
        </tbody>
      </table>
      <div className="text-center">
        <button
          type="button"
          className="btn btn-primary"
          onClick={handleSubmit}
        >
          Submit
        </button>
      </div>
    </div>
  );
}
