import React from "react";
import "./App.css";
import { Route, Redirect, Switch } from "react-router-dom";
import Header from "./header/header";
import AllBooks from "./bookList/allBooks";
import OrderDetail from "./header/orderDetail";

function App() {
  return (
    <React.Fragment>
      <Header />

      <Switch>
        <Route path="/allBooks" exact component={AllBooks} />
        <Route path="/orderDetails" exact component={OrderDetail} />
        <Redirect from="/" exact to="/Products" />
      </Switch>
    </React.Fragment>
  );
}

export default App;
