import React, { FC } from "react";

interface Props {
  search: string;
  handleChange: any;
}

const SearchBox: FC<Props> = (props) => {
  return (
    <input
      type="text"
      name="query"
      autoComplete="off"
      className="form-control h-25 w-50 mx-5 my-2  ml-5"
      placeholder="search..."
      value={props.search}
      onChange={(e) => props.handleChange(e.target.value)}
    />
  );
};
export default SearchBox;
