import React, { FC } from "react";
import { Button } from "react-bootstrap";
import Modal from "react-bootstrap/Modal";
import { useState, useEffect } from "react";

interface Props {
  bookdata: any;
  closeModal: any;
}

const CartModal: FC<Props> = (props) => {
  const [quantity, setQuantity] = useState<number>(1);

  const [bookCart] = useState(JSON.parse(localStorage.getItem("cart") || "[]"));
  useEffect(() => {
    let ind = bookCart.findIndex((book: any) => book.id === props.bookdata.id);

    setQuantity(ind !== -1 ? bookCart[ind].qty : 1);
    console.log("abcd", bookCart[ind] || []);
  }, []);

  const incrementItem = () => {
    console.log("IncrementItem");
    setQuantity((prevState: number) => {
      if (prevState < 6) {
        return prevState + 1;
      } else {
        return prevState;
      }
    });
  };
  const decreaseItem = () => {
    console.log("DecreaseItem");
    setQuantity((prevState: number) => {
      if (prevState > 1) {
        return prevState - 1;
      } else {
        return 1;
      }
    });
  };

  const handleOrder = (bdata: any) => {
    const order = { ...bdata, qty: quantity };
    console.log("A", order);

    let ind = bookCart.findIndex((book: any) => book.id === order.id);
    if (ind !== -1) {
      bookCart[ind] = order;
    } else {
      bookCart.push(order);
    }
    localStorage.setItem("cart", JSON.stringify(bookCart));
    alert("Your Item added into Cart:)");
    props.closeModal();
  };

  return (
    <>
      <Modal.Header closeButton>
        <Modal.Title>{props.bookdata.title}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="container w-100 d-flex justify-content-start ">
          <div className="row mr-1">
            <div className="col col-lg-2  font-weight-bold mr-3">Author</div>
            <div className="col-md-auto "> : {props.bookdata.author_name} </div>
            <div className="w-100"></div>
            <div className="col col-lg-2 font-weight-bold mr-3">Ratings</div>
            <div className="col-md-auto">: {props.bookdata.ratings}</div>
            <div className="w-100"></div>
            <div className="col col-lg-2 font-weight-bold mr-3">Price</div>
            <div className="col-md-auto">: {props.bookdata.price}</div>
            <div className="w-100"></div>
            <div className="col col-lg-2 font-weight-bold  mr-3">
              Description
            </div>
            <div className="col col-lg-8  w-100 text-justify">
              : {props.bookdata.desc}
            </div>
          </div>
        </div>
        <div className=" qty-btn">
          <button
            className="btn btn-primary  btn-sm m-2"
            onClick={decreaseItem}
          >
            -
          </button>

          {quantity}

          <button
            className="btn btn-primary  btn-sm m-2"
            onClick={incrementItem}
          >
            +
          </button>
        </div>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.closeModal}>
          Close
        </Button>
        <Button variant="primary" onClick={() => handleOrder(props.bookdata)}>
          Confirm
        </Button>
      </Modal.Footer>
    </>
  );
};

export default CartModal;
