import React from "react";
import { Button } from "react-bootstrap";
import Modal from "react-bootstrap/Modal";
import { getBookData } from "../bookdatas/b_data";
import { useState } from "react";
import SearchBox from "./searchBox";
import CartModal from "../cartDialog/cartModal";

export default function AllBooks() {
  const [bookdata, setData] = useState(getBookData());
  const [search, setSearch] = useState("");
  const [sortVal, setSortVal] = useState("asc");
  const [filterdata, setfiltered] = useState(getBookData());
  const [quantity, setQuantity] = useState<number>(1);
  const [showModal, setModal] = useState(0);
  const [bookCart] = useState(JSON.parse(localStorage.getItem("cart") || "[]"));

  const handleSearch = (s: any) => {
    setSearch(s);
    console.log("search", s);
    let filtered = filterdata;
    if (s) {
      filtered = bookdata.filter(
        (b) =>
          b.title.toLowerCase().startsWith(s.toLowerCase()) ||
          b.author_name.toLowerCase().startsWith(s.toLowerCase())
      );
      console.log("fil", filtered);
      setfiltered(filtered);
    } else {
      setfiltered(bookdata);
    }
  };

  const handleSorting = (val: any) => {
    console.log(val);
    if (sortVal === "asc") {
      let ascSorted = bookdata.sort((a, b) => a.ratings - b.ratings);
      console.log(ascSorted);
      setData(ascSorted);
      setSortVal("desc");
    } else {
      let descSorted = bookdata.sort((a, b) => b.ratings - a.ratings);
      console.log(descSorted);
      setData(descSorted);
      setSortVal("asc");
    }
  };
  const closeModal = () => {
    setModal(0);
  };
  const openModal = (value: number) => {
    console.log("id", value);

    let ind = bookCart.findIndex((book: any) => book.id === value);
    console.log("idnnn", ind);

    setQuantity(ind !== -1 ? bookCart[ind].qty : 1);
    console.log("abcdeee", bookCart[ind] || []);
    console.log(quantity, "qqq");
    setModal(value);
  };

  return (
    <>
      <div className="search-sort w-50 d-flex justify-content-end mt-5 pt-3">
        <SearchBox search={search} handleChange={handleSearch} />

        <button
          type="button"
          className="btn btn-primary mt-1 "
          value={sortVal}
          onClick={() => handleSorting(sortVal)}
        >
          Ratings by "{sortVal}" order
        </button>
      </div>
      <div className="book ">
        {filterdata.length !== 0 ? (
          filterdata.map((bookdata, key) => {
            return (
              <div className="mx-5 my-3 ">
                <div className="mx-5 my-3">
                  <div key={key} className="card mx-2 my-2 p-1 float-left ">
                    <img
                      className="card-img-top "
                      style={{ width: "340px", height: "250px" }}
                      src={bookdata.book_img}
                      alt="Card image cap"
                    />
                    <div className="card-body ">
                      <p className="card-text float-left font-weight-bold ">
                        Title
                      </p>
                      <p className="card-text-p ">: {bookdata.title}</p>
                      <p className="card-text float-left font-weight-bold ">
                        Brand
                      </p>
                      <p className="card-text-p">: {bookdata.author_name}</p>
                      <p className="card-text float-left font-weight-bold ">
                        Ratings
                      </p>
                      <p className="card-text-p">: {bookdata.ratings}</p>
                      <p className="card-text float-left font-weight-bold ">
                        Price
                      </p>
                      <p className="card-text-p">: {bookdata.price}</p>
                      <Button
                        variant="primary"
                        onClick={() => openModal(bookdata.id)}
                      >
                        Add to Cart
                      </Button>
                      <Modal
                        show={showModal === bookdata.id}
                        onHide={closeModal}
                        backdrop="static"
                      >
                        {showModal && (
                          <CartModal
                            bookdata={bookdata}
                            closeModal={closeModal}
                          />
                        )}
                      </Modal>
                    </div>
                  </div>
                </div>
              </div>
            );
          })
        ) : (
          <h1 className="d-flex justify-content-centerd-flex justify-content-center mt-5 pt-5">
            Product Not Found
          </h1>
        )}
      </div>
    </>
  );
}
